.more-books {
  width: 100%;

  &__title {
    text-align: left;
    margin: 0 $indent;
  }

  &__link {
    text-align: center;
    margin: 0 $indent;
  }

    &__block {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    margin: 0 auto;

    @media (min-width: 1200px) {
      width: 1170px;
    }

    .mini-card {
      width: 100%;

      @media (min-width: 768px) {
        width: 50%;
      }

      @media (min-width: 1200px) {
        width: 25%;
      }
    }
  }
}
