.field-text {

  $block-name: &;

  position: relative;
  display: block;

  &__input {
    display: inline-block;
    width: 100%;
    height: 32px;
    margin: 0;
    border: none;
    border-bottom: 4px solid $gray-light;
    background-color: $gray-lightest;
    padding: 6px 0;
    font-size: $font-size;
    font-family: inherit;
    line-height: 1;
    color: $text-color;
    transition: all 0.3s;

    &:hover,
    &:focus {
      outline: none;
      border-bottom-color: $main-color;
    }

    &::placeholder {
      color: $gray;
      opacity: 1;
    }
  }

  &__msg {
    position: absolute;
    top: 6px;
    left: 0;
    width: 100%;
    font-size: $font-size-sm;
    line-height: 1;
    color: $danger-color;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &--error {

    #{$block-name}__input {
      border-bottom-color: $danger-color;
    }
  }
}